export default {
	data() {
		return {
			isSecond:false,
			imgUrl:[],
			test: '',
			isPaySuccess:true,
			contentItem:[{
				title:'绑定亲友号（选填）',
				placeholderText:'请输入亲友手机号'
			},{
				title:'验证码',
				placeholderText:'请输入收到的验证码'
			}]
		}
	},
	methods:{
		onVipAlready(){
			console.log(this.isPaySuccess)
			let isPaySuccess = this.isPaySuccess
			uni.redirectTo({
				url: '../bevip/bevip?={{isPaySuccess}}'
			})
		},
		bindImage(){
			uni.chooseImage({
			    count: 1,
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: res=> {
			        console.log(res)
					this.imgUrl[0] = res.tempFilePaths[0]
					this.isSecond = true
			    }
			})
		},
		bindChangeImage(){
			uni.showActionSheet({
			    itemList: ['重新上传', '预览图片'],
			    success: res=> {
			        // console.log(res.tapIndex)
					if(res.tapIndex == 0){
						console.log(this.imgUrl[0])
						this.imgUrl[0] = ''
						console.log(this.imgUrl[0])
						this.bindImage()
					}else{
						uni.previewImage({
							urls:this.imgUrl
						})
					}
			    }
			})
		}
	},
}
